<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')" class="current">
				운동친구 멤버
			</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<div class="member-web">
				<p class="contents-title underline marB-20 body3">
					운동 친구 멤버페이지
				</p>
				<div class="input-container marB-40">
					<div class="img-upload-container">
						<p class="body3">웹 이미지</p>
						<div class="input-item align-top">
							<AddImageSingle
								v-if="webImage !== 'none'"
								:refName="'webImage'"
								:image="webImage"
								@webImage="setWebImage"
							/>

							<button class="outlined-btn" @click="updateMember(1)">
								저장
							</button>
						</div>
					</div>
					<div class="img-upload-container">
						<p class="body3">모바일 이미지</p>
						<div class="input-item align-top">
							<AddImageSingle
								v-if="mobileImage !== 'none'"
								:refName="'mobileImage'"
								:image="mobileImage"
								@mobileImage="setMobileImage"
							/>

							<button class="outlined-btn" @click="updateMember(2)">
								저장
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Store from '@/store';
import { mapGetters } from 'vuex';
import { fetchMember, updateMember } from '@/api/member';
import AddImageSingle from '@/components/common/AddImageSingle';

export default {
	created() {
		this.fetchMember();
	},
	components: { AddImageSingle },
	computed: {
		...mapGetters(['web', 'mobile']),
	},
	data() {
		return {
			mobileImage: 'none',
			webImage: 'none',
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchMember() {
			let { data } = await fetchMember();
			this.webImage = data[0];
			this.mobileImage = data[1];
			console.log(this.webImage);
			console.log(this.mobileImage);
		},
		async updateMember(id) {
			let data = new FormData();
			data.append('id', id);
			if (id == 1) {
				data.append('file', this.web);
			} else {
				data.append('file', this.mobile);
			}

			try {
				await updateMember(data);
			} catch (e) {
				console.log(e);
			}
		},
		setWebImage(data) {
			Store.commit('SET_WEB', data);
		},
		setMobileImage(data) {
			Store.commit('SET_MOBILE', data);
			console.log(this.mobile);
		},
	},
};
</script>

<style></style>
